@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  align-items: center;
  text-align: center;
}

main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}

:root {
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

html {
  background: url(/react-profile/static/media/Phoenix.8ee8f1b8.jpeg) no-repeat center center fixed;
  background-size: cover;
}

body {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  min-height: 100%;
  padding: 0;
  color: white;
  text-shadow: 1px 1px 1px black;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: .1em;
}

h1 {
  font-size: 2.5rem;
  color: white;
  text-shadow: 1px 2px 5px black;
}

h2 {
  font-size: 1.5rem;
  color: white;
}

p {
  width: 100%;
  padding: 5%;
  font-size: 1.5rem;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.navActive {
  color: var(--secondary);
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: calc(2.5rem);
  margin-top: var(--spacing-five);
  margin-bottom: calc(2.5rem);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: calc(1rem * 0.5);
  padding-right: var(--spacing-one);
  padding-left: calc(1rem * 0.5);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

.container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.card {
  background-color: whitesmoke;
  border-radius: 8px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem;
  width: 20rem;
  margin: 2rem;

}

.card p {
  color:rgba(0, 0, 0, 0.2);
  font-weight: normal;
  font-size: 1rem;
}
.card h3 {
  color:rgba(0, 0, 0, 0.2);
  font-weight: Bold;
  font-size: 1.5rem;
}

.modal {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  background-color: white;
  padding: 1rem;
  text-align: center;
  width: 30rem;
  z-index: 10;
  position: fixed;
  top: 20vh;
  left: calc(50% - 15rem);
}

.backdrop {
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

form {
  width: 100%;
  padding: 5px;
  margin: 5px;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid black;
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid black;
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: rgb(130, 231, 130);
}
.MainNavigation_header__pcWBd {
  width: 100%;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #083f4d;
  padding: 0 10%;
}

.MainNavigation_logo__3mTft {
  font-size: 1.5rem;
  color: white;
  font-weight: bold;
}

header h1 {
    color: white;
    font-size: 2rem;
}

.MainNavigation_header__pcWBd ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: baseline;
}

.MainNavigation_header__pcWBd li {
  margin-left: 3rem;
}

.MainNavigation_header__pcWBd a {
  text-decoration: none;
  font-size: 1.5rem;
  color: white;
}

.MainNavigation_header__pcWBd a:hover,
.MainNavigation_header__pcWBd a:active,
.MainNavigation_header__pcWBd a.MainNavigation_active__oN3Ka {
   color: #e2d87e;
}

.MainNavigation_badge__QYsXO {
  background-color: #083f4d;
  color: white;
  border-radius: 12px;
  padding: 0 1rem;
  margin-left: 0.5rem;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

